/* chat.css */
/* reset */
html,body{width: 100%; height: 100%;}
body,div,dl,dt,dd,ul,li,ol,h1,h2,h3,h4,h5,h6,pre,form,fieldset,legend,label,table,caption,tbody,tfoot,thead,th,tr,td,summary,input,textarea,p,blockquote,abbr,address,cite,code,del,dfn,em,img,ins,kbd,q,samp,strong,sub,sup,var,th,td,table,small,b,i,article,aside,canvas,details,figcaption,figure,footer,header,menu,nav,section,time,mark,audio,video{margin:0;padding:0;}
article,aside,details,figure,figcaption,footer,header,menu,nav,section,video{display: block;}
table{border-collapse: collapse;}
img,fieldset{border:none;}
address,caption,cite,code,em,strong,th{font-size: normal; font-weight: normal;}
ol,ul{list-style: none;}
caption,th{text-align: left;}
h1,h2,h3,h4,h5,h6{font-weight: normal; font-size: 100%;}
q:after,q:before{content:"";}
abbr,acronym{border:none;}

img,input{vertical-align: middle;}
hr{display:none;}

#skipNavi,.blind{position: absolute; left: 0; top: 0; width: 0; height: 0; font-size: 0; list-style: 0; text-indent: -9999px; overflow: hidden; visibility: hidden;}

li img,dd img{vertical-align: top;}
img{max-width: 100%;}
/* common */
*{-webkit-text-size-adjust: none;}
body{color: #8E9399; font-size: 12px; font-family: 'Noto Sans', sans-serif;}
a{color: #333; text-decoration: none;}
img{width: 40px; height: 40px; border-radius: 40px; margin-right: 10px; margin-left: 15px;}
main {
   animation: ani .5s ease-in-out;
 }
 
 @keyframes ani {
   from {
     opacity: 0;
     transform: translateY(10px);
   }
   to {
     opacity: 1;
     transform: none;
   }
 }

/* layout */
body>header{width: 100%;}
body>main{width: 100%;}
body>nav{width: 100%;}

/* header */
header.top{background-color: #513736; color: #fff;}

.header_top_c{display: flex; justify-content: space-between; padding: 5px 10px 0;}
.header_top_c div:nth-child(2){margin-left: 50px;}
.header_top_c div i{padding-right: 3px;}

.header_bottom_c{display: flex; align-items: baseline; padding: 10px 20px;}
.header_bottom_c .header_txt{font-size: 16px;}
.header_bottom_c div:nth-of-type(1) span{flex: 1; color: rgb(165, 146, 146);}
.header_bottom_c div:nth-of-type(2){display: flex; align-items: center; margin: 0 auto; padding-right: 20px;}
.header_bottom_c div:nth-of-type(2) span{margin-right: 3px;}

/* main */
/* 검색 */
.search-bar_c{width: 95%; margin: 13px auto; display: flex; flex-direction: column; justify-content: center; align-items: center;}
.search-bar_c i{font-size: 15px; color: rgb(189, 189, 189); position: absolute; left: 28px;}
.search-bar_c input{width: 100%; background-color: rgb(245, 245, 245); border: none; border-radius: 5px; padding: 7px 0; transition: all 0.3s linear 0s;}
input::placeholder{text-align: center;}
.search-bar_c input:focus{outline: none; box-shadow: 0 0 0pt 1pt #FFE948;}

/* friends_list */
.friend-list{display: flex; flex-direction: column;}

.friend-list div a{display: flex; justify-content: space-between; padding: 15px; border-top: 1px solid #eee;}
.friend-list div a div{display: flex; flex: 2; flex-direction: column; align-items: flex-start;}
.friend-list div:last-of-type a{border-bottom: 1px solid #eee;}

.friend-list img{width: 50px; height: 50px;}
.friend-list a div{margin-left: 10px;}
.friend-list .name_c{font-size: 16px; font-weight: bold; padding-bottom: 5px;}
.friend-list div .talk_c,.time_c{display:inline-block; font-size: 11px; color: rgb(197, 197, 197);}
.friend-list .time_c{margin-right: 5px; font-size: 10px;}

/* talk_btn */
.talk_btn{position: absolute; right: 20px; bottom: 80px;}
.talk_btn i{position: absolute; left: 50%; top: 50%; transform: translate3d(-50%,-50%,0); font-size: 20px; color: #513736;}
.talk_btn a{display: block; background: #FFE948; width: 60px; height: 60px; border-radius: 60px;  box-shadow: 0px 0px 10px #ccc; transition: all 0.2s linear 0s;}

.talk_btn a:hover{background: #513736;}
.talk_btn a:hover i{color: #fff;}


